/*
 * Gridism
 * A simple, responsive, and handy CSS grid by @cobyism
 * https://github.com/cobyism/gridism
 */
/* Preserve some sanity */
.grid,
.unit {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

/* Set up some rules to govern the grid */
.grid {
  display: block;
  clear: both; }

.grid .unit {
  float: left;
  width: 100%;
  padding: 10px; }

/* This ensures the outer gutters are equal to the (doubled) inner gutters. */
.grid .unit:first-child {
  padding-left: 20px; }

.grid .unit:last-child {
  padding-right: 20px; }

/* Nested grids already have padding though, so let’s nuke it */
.unit .unit:first-child {
  padding-left: 0; }

.unit .unit:last-child {
  padding-right: 0; }

.unit .grid:first-child > .unit {
  padding-top: 0; }

.unit .grid:last-child > .unit {
  padding-bottom: 0; }

/* Let people nuke the gutters/padding completely in a couple of ways */
.no-gutters .unit,
.unit.no-gutters {
  padding: 0 !important; }

/* Wrapping at a maximum width is optional */
.wrap .grid,
.grid.wrap {
  max-width: 978px;
  margin: 0 auto; }

/* Width classes also have shorthand versions numbered as fractions
 * For example: for a grid unit 1/3 (one third) of the parent width,
 * simply apply class="w-1-3" to the element. */
.grid .whole, .grid .w-1-1 {
  width: 100%; }

.grid .half, .grid .w-1-2 {
  width: 50%; }

.grid .one-third, .grid .w-1-3 {
  width: 33.3332%; }

.grid .two-thirds, .grid .w-2-3 {
  width: 66.6665%; }

.grid .one-quarter, .grid .w-1-4 {
  width: 25%; }

.grid .three-quarters, .grid .w-3-4 {
  width: 75%; }

.grid .one-fifth, .grid .w-1-5 {
  width: 20%; }

.grid .two-fifths, .grid .w-2-5 {
  width: 40%; }

.grid .three-fifths, .grid .w-3-5 {
  width: 60%; }

.grid .four-fifths, .grid .w-4-5 {
  width: 80%; }

.grid .golden-small, .grid .w-g-s {
  width: 38.2716%; }

/* Golden section: smaller piece */
.grid .golden-large, .grid .w-g-l {
  width: 61.7283%; }

/* Golden section: larger piece */
/* Clearfix after every .grid */
.grid {
  *zoom: 1; }

.grid:before, .grid:after {
  display: table;
  content: "";
  line-height: 0; }

.grid:after {
  clear: both; }

/* Utility classes */
.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.pull-left {
  float: left; }

.pull-right {
  float: right; }

/* Responsive Stuff */
@media screen and (max-width: 568px) {
  /* Stack anything that isn’t full-width on smaller screens */
  .grid .unit {
    width: 100% !important;
    padding-left: 20px;
    padding-right: 20px; }
  .unit .grid .unit {
    padding-left: 0px;
    padding-right: 0px; }
  /* Sometimes, you just want to be different on small screens */
  .center-on-mobiles {
    text-align: center !important; }
  .hide-on-mobiles {
    display: none !important; } }

/* Expand the wrap a bit further on larger screens */
@media screen and (min-width: 1180px) {
  .wider .grid {
    max-width: 1180px;
    margin: 0 auto; } }

.hamburger {
  padding: 10px 10px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible; }
  .hamburger:hover {
    opacity: 1; }

.hamburger-box {
  width: 25px;
  height: 20px;
  display: inline-block;
  position: relative; }

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px; }
  .hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
    width: 25px;
    height: 4px;
    background-color: #2D3943;
    border-radius: 5px;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease; }
  .hamburger-inner::before, .hamburger-inner::after {
    content: "";
    display: block; }
  .hamburger-inner::before {
    top: -8px; }
  .hamburger-inner::after {
    bottom: -8px; }

/*
   * Squeeze
   */
.hamburger--squeeze .hamburger-inner {
  transition-duration: 0.075s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19); }
  .hamburger--squeeze .hamburger-inner::before {
    transition: top 0.075s 0.12s ease, opacity 0.075s ease; }
  .hamburger--squeeze .hamburger-inner::after {
    transition: bottom 0.075s 0.12s ease, transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19); }

.hamburger--squeeze.is-active .hamburger-inner {
  transform: rotate(45deg);
  transition-delay: 0.12s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  .hamburger--squeeze.is-active .hamburger-inner::before {
    top: 0;
    opacity: 0;
    transition: top 0.075s ease, opacity 0.075s 0.12s ease; }
  .hamburger--squeeze.is-active .hamburger-inner::after {
    bottom: 0;
    transform: rotate(-90deg);
    transition: bottom 0.075s ease, transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1); }

.tooltipped {
  position: relative; }

.tooltipped::after {
  position: absolute;
  z-index: 1000000;
  display: none;
  padding: 0.5em 0.75em;
  font: normal normal 11px/1.5 -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  -webkit-font-smoothing: subpixel-antialiased;
  color: #fff;
  text-align: center;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: break-word;
  white-space: pre;
  pointer-events: none;
  content: attr(aria-label);
  background: #1b1f23;
  border-radius: 3px;
  opacity: 0; }

.tooltipped::before {
  position: absolute;
  z-index: 1000001;
  display: none;
  width: 0;
  height: 0;
  color: #1b1f23;
  pointer-events: none;
  content: "";
  border: 6px solid transparent;
  opacity: 0; }

@-webkit-keyframes tooltip-appear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes tooltip-appear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.tooltipped:hover::before, .tooltipped:hover::after, .tooltipped:active::before, .tooltipped:active::after, .tooltipped:focus::before, .tooltipped:focus::after {
  display: inline-block;
  text-decoration: none;
  -webkit-animation-name: tooltip-appear;
  animation-name: tooltip-appear;
  -webkit-animation-duration: .1s;
  animation-duration: .1s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in;
  animation-timing-function: ease-in;
  -webkit-animation-delay: .4s;
  animation-delay: .4s; }

.tooltipped-no-delay:hover::before, .tooltipped-no-delay:hover::after, .tooltipped-no-delay:active::before, .tooltipped-no-delay:active::after, .tooltipped-no-delay:focus::before, .tooltipped-no-delay:focus::after {
  -webkit-animation-delay: 0s;
  animation-delay: 0s; }

.tooltipped-multiline:hover::after, .tooltipped-multiline:active::after, .tooltipped-multiline:focus::after {
  display: table-cell; }

.tooltipped-s::after, .tooltipped-se::after, .tooltipped-sw::after {
  top: 100%;
  right: 50%;
  margin-top: 6px; }

.tooltipped-s::before, .tooltipped-se::before, .tooltipped-sw::before {
  top: auto;
  right: 50%;
  bottom: -7px;
  margin-right: -6px;
  border-bottom-color: #1b1f23; }

.tooltipped-se::after {
  right: auto;
  left: 50%;
  margin-left: -16px; }

.tooltipped-sw::after {
  margin-right: -16px; }

.tooltipped-n::after, .tooltipped-ne::after, .tooltipped-nw::after {
  right: 50%;
  bottom: 100%;
  margin-bottom: 6px; }

.tooltipped-n::before, .tooltipped-ne::before, .tooltipped-nw::before {
  top: -7px;
  right: 50%;
  bottom: auto;
  margin-right: -6px;
  border-top-color: #1b1f23; }

.tooltipped-ne::after {
  right: auto;
  left: 50%;
  margin-left: -16px; }

.tooltipped-nw::after {
  margin-right: -16px; }

.tooltipped-s::after, .tooltipped-n::after {
  -webkit-transform: translateX(50%);
  transform: translateX(50%); }

.tooltipped-w::after {
  right: 100%;
  bottom: 50%;
  margin-right: 6px;
  -webkit-transform: translateY(50%);
  transform: translateY(50%); }

.tooltipped-w::before {
  top: 50%;
  bottom: 50%;
  left: -7px;
  margin-top: -6px;
  border-left-color: #1b1f23; }

.tooltipped-e::after {
  bottom: 50%;
  left: 100%;
  margin-left: 6px;
  -webkit-transform: translateY(50%);
  transform: translateY(50%); }

.tooltipped-e::before {
  top: 50%;
  right: -7px;
  bottom: 50%;
  margin-top: -6px;
  border-right-color: #1b1f23; }

.tooltipped-align-right-1::after, .tooltipped-align-right-2::after {
  right: 0;
  margin-right: 0; }

.tooltipped-align-right-1::before {
  right: 10px; }

.tooltipped-align-right-2::before {
  right: 15px; }

.tooltipped-align-left-1::after, .tooltipped-align-left-2::after {
  left: 0;
  margin-left: 0; }

.tooltipped-align-left-1::before {
  left: 5px; }

.tooltipped-align-left-2::before {
  left: 10px; }

.tooltipped-multiline::after {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  max-width: 250px;
  word-wrap: break-word;
  white-space: pre-line;
  border-collapse: separate; }

.tooltipped-multiline.tooltipped-s::after, .tooltipped-multiline.tooltipped-n::after {
  right: auto;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%); }

.tooltipped-multiline.tooltipped-w::after, .tooltipped-multiline.tooltipped-e::after {
  right: 100%; }

@media screen and (min-width: 0\0) {
  .tooltipped-multiline::after {
    width: 250px; } }

.tooltipped-sticky::before, .tooltipped-sticky::after {
  display: inline-block; }

.tooltipped-sticky.tooltipped-multiline::after {
  display: table-cell; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background-color: #2D3943;
  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 18px;
  scroll-behavior: smooth; }

#content_editor {
  width: 100%;
  max-width: 816px;
  margin: 0 auto;
  margin-top: 10rem; }
  #content_editor img {
    max-width: 100%; }
  #content_editor ul {
    padding-left: 40px; }

.page {
  background-color: #fff;
  padding: 20px 30px;
  position: relative;
  margin-bottom: 10rem; }
  .page:before {
    content: attr(data-title);
    position: absolute;
    background-color: #6BBE45;
    left: 0;
    right: 0;
    padding: 0.5em 1rem;
    top: -40px;
    color: #fff; }

.page-list {
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.5);
  z-index: 100; }
  .page-list button {
    outline: none;
    display: flex;
    padding-top: 1rem;
    padding-bottom: 1rem;
    line-height: 1; }
    .page-list button .hamburger-box {
      padding-left: 1em;
      margin-right: 2em; }
  .page-list #copyCSS {
    font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
    font-size: 14px;
    padding: 1em;
    background-color: #006B4E;
    border: 0;
    color: #fff;
    cursor: pointer;
    transition: all 150ms ease-in-out; }
    .page-list #copyCSS:hover {
      background-color: #000504; }

#navigation {
  position: absolute;
  top: 52px;
  transform: translateX(calc(-100% - 1px));
  transition: all 150ms ease-in-out;
  background-color: #fff;
  font-size: 0.9em;
  min-width: 250px;
  max-width: 275px;
  overflow: scroll;
  max-height: calc(100vh - 52px);
  box-shadow: 5px 5px 5px transparent; }
  #navigation ul {
    list-style-type: none; }
  #navigation li:first-child a {
    border-top: 1px solid #ccc; }
  #navigation li:last-child a {
    border-bottom: 0; }
  #navigation a {
    display: block;
    padding: 0.5em 2em;
    color: #006B4E;
    text-decoration: none;
    transition: all 200ms ease-in-out;
    border-bottom: 1px solid #ccc; }
    #navigation a:hover {
      background-color: #006B4E;
      color: #fff; }
  .is-active + #navigation {
    transform: translateX(0%);
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.2); }

.copyHTML {
  position: absolute;
  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
  top: -30px;
  right: 1rem;
  padding: 0.5em;
  line-height: 1;
  font-size: 0.65rem;
  cursor: pointer;
  color: #006B4E;
  background-image: none;
  background-color: #fff;
  border: 0;
  border-radius: 3px;
  transition: all 150ms ease-in-out; }
  .copyHTML:hover {
    background-color: #e6e6e6; }

@media screen and (max-width: 735px) {
  .copyHTML {
    display: none; } }
